import { toYupSchema } from '@everlutionsk/helpers-yup';
import type { Ref, Schema } from 'yup';

export function createFormSpec<T extends object>(rules: Record<keyof T, Schema<any> | Ref>) {
  const validationSchema = toYupSchema(rules);
  return (initialValues: Record<keyof T, any>) => ({ initialValues, validationSchema });
}
